import React from 'react';

import './todoList.scss';

import TodoItem from '../todo-item/todo-item.js';

const TodoList = ({todos,modalToggle,setCurrentTodo}) => {
  const mappedList = todos.map(el => <TodoItem setCurrentTodo={setCurrentTodo} key={el.id} todo={el} modalToggle={modalToggle}/>)
  return <div className="TodoList">{mappedList}</div>
}

export default TodoList;